import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { Routes, RouterModule } from '@angular/router';
import { AddBinTransferComponent } from './add-bin-transfer.component';
import { ReactiveFormsModule } from '@angular/forms';
import { FormValidationModule } from 'src/app/shared/form-validation/form-validation.module';
import { SharedModule } from 'src/app/shared/shared.module';
import { NgSelectModule } from '@ng-select/ng-select';
import { BinTransferService } from 'src/app/service/bin-transfer.service';

const routes: Routes = [
  {
    path: '',
    component: AddBinTransferComponent,
    data: { title: 'bin_tranfer_add' },
  }
]

@NgModule({
  declarations: [AddBinTransferComponent],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    ReactiveFormsModule,
    FormValidationModule,
    SharedModule,
    NgSelectModule
  ],
   providers: [BinTransferService]
})
export class AddBinTransferModule { }
